import React from 'react'
import './Ttt.scss'
import ai from './img/ai.svg'
import XandO from './XandO'

function Ttt() {
    return (
        <>
        <div id='frontPage'>
            <h1>Game Modes</h1>
            <div id='bot'><h1>Player Vs Bot</h1></div>
            <div id='player'><h1>Player Vs Player</h1></div>
        </div>
        <div id="front">
            <div id="title1" className="reset"><XandO nameClass='xTitle' nameSvgO='oTitle' nameSvgX='xTitle' nameTitle='Turn'/></div>
            <div className='resetContainer'>
                <div id="resetBtn1" className="reset">↺</div>
                <div id="aI1" className="reset"><img id='img1' src={ai}/></div>
            </div> 
        </div>
        <div className="block">
            <div id="title"><XandO nameSvgO='oTitle' nameSvgX='xTitle' nameTitle='Turn'/></div>
            <div id='score'>
                <div id='aiScore'>
                    <div id='playerScore'>
                        Player-Score = 0
                    </div>
                    <div id='botScore'>
                        Bot-Score = 0
                    </div>
                </div>
                <div id='notAiScore'>
                    <div id='xScore'>
                        X-Score = 0
                    </div>
                    <div id='oScore'>
                        O-Score = 0
                    </div>
                </div>
                
            </div>
            <div className="row1">
                <span id="btn1" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO1' nameSvgX='spanSvgX1' nameO='spanO1' nameX1='spanX1' nameX2='spanX21'/></span>
                <span id="btn2" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO2' nameSvgX='spanSvgX2' nameO='spanO2' nameX1='spanX2' nameX2='spanX22'/></span>
                <span id="btn3" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO3' nameSvgX='spanSvgX3' nameO='spanO3' nameX1='spanX3' nameX2='spanX23'/></span>
            </div> 
            <div className="row2"> 
                <span id="btn4" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO4' nameSvgX='spanSvgX4' nameO='spanO4' nameX1='spanX4' nameX2='spanX24'/></span>
                <span id="btn5" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO5' nameSvgX='spanSvgX5' nameO='spanO5' nameX1='spanX5' nameX2='spanX25'/></span>
                <span id="btn6" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO6' nameSvgX='spanSvgX6' nameO='spanO6' nameX1='spanX6' nameX2='spanX26'/></span>
            </div> 
            <div className="row3"> 
                <span id="btn7" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO7' nameSvgX='spanSvgX7' nameO='spanO7' nameX1='spanX7' nameX2='spanX27'/></span>
                <span id="btn8" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO8' nameSvgX='spanSvgX8' nameO='spanO8' nameX1='spanX8' nameX2='spanX28'/></span>
                <span id="btn9" className="btn"><XandO nameGo='gO' nameGx='gX' nameSvgO='spanSvgO9' nameSvgX='spanSvgX9' nameO='spanO9' nameX1='spanX9' nameX2='spanX29'/></span>
            </div>
            <div>
                <div id="resetBtn" className="Btn">↺</div>
                <div id="aI"><img id='img' src={ai}/></div>
            </div>
        </div>
        </>
    )
}

export default Ttt
